// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import '../src/assets/styles/reset.css'
import '../src/assets/styles/border.css'
import '../src/assets/styles/iconfont.css'
import headerComponent from '@/pages/components/header';
import Swiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import homeSwiper from '@/pages/components/swiper';
import honeIcons from '@/pages/components/icons';
import mintUi from 'mint-ui';
import Axios from 'axios';
import 'mint-ui/lib/style.min.css';
import store from './store/vuex.js';
import footer from './components/footer';

Vue.prototype.$axios = axios;
Vue.config.productionTip = false;
Vue.use(Swiper);
Vue.use(mintUi);
// 字符串拼接过滤器
Vue.filter('cutStr', function (strqwe, len, hasDot) {
	if (!strqwe) {
		return '　　';
	} else {
		hasDot = hasDot || 1;
		var newLength = 0;
		var newStr = "";
		var chineseRegex = /[^\x00-\xff]/g;
		var singleChar = "";
		var strLength = strqwe.replace(chineseRegex, "**").length;
		for (var i = 0; i < strLength; i++) {
			singleChar = strqwe.charAt(i).toString();
			if (singleChar.match(chineseRegex) != null) {
				newLength += 2;
			} else {
				newLength++;
			}
			if (newLength > len) {
				break;
			}
			newStr += singleChar;
		}
		if (hasDot && strLength > len) {
			newStr += "...";
		}
		newStr = newStr.replace(/\s+/g, "");
		return newStr;
	}
})
// Axios.defaults.baseURL = '/api';
Axios.defaults.baseURL = '/static/mock';
// 拦截器 显示loading
Axios.interceptors.request.use(function(config){
	mintUi.Indicator.open({
	  text:'加载中...',
	  spinnerType:'fadubf-circle'
	});
	return config;
  });
  Axios.interceptors.response.use(function(config){
	  setTimeout(()=>{
		mintUi.Indicator.close();
	  },500)
	return config;
  });  
Vue.prototype.backTo = function(){
	router.go(-1);
}  
Vue.component('headerComponent', headerComponent)
Vue.component('homeSwiper', homeSwiper)
Vue.component('honeIcons', honeIcons)
Vue.component('footerComponent',footer)

router.beforeEach((to, from, next) => {
	if (to.matched.some(res => res.meta.requireAuth)) { // 验证是否需要登陆
	  if (localStorage.getItem('isLogin')) { // 查询本地存储信息是否已经登陆
		next();
	  } else {
		next({
		  path: '/login', // 未登录则跳转至login页面
		  query: {redirect: to.fullPath} // 登陆成功后回到当前页面，这里传值给login页面，to.fullPath为当前点击的页面
          });
	  }
	} else {	
	  next();
	}
  });
/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	components: { App },
	template: '<App/>'
})



