<!--  -->
<template>
  <div class="maylike">
    <div class="title" v-if="!isHome">
      <img src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png" alt>
      猜你喜欢
    </div>
	 <h5 v-if="isHome">看了该景点的人还看了</h5>
    <div class="likeList">
      <ul>
        <router-link :to="{'name':'Detail',query:{img:item.imgUrl,title:item.title,sId:item.id}}" tag="li" v-for="(item) in mayLikeList" :key="item.imgUrl">
          <div class="item">
            <img class="itemCover" v-lazy="item.imgUrl" alt>
            <div class="coreDetail">
              <p class="title">{{item.title}}</p>
              <div class="core">
                <i class="iconfont" v-if="item.avgScore>0">&#xe626;</i>
                <i class="iconfont" v-if="item.avgScore>1">&#xe626;</i>
                <i class="iconfont" v-if="item.avgScore>2">&#xe626;</i>
                <i class="iconfont" v-if="item.avgScore>3">&#xe626;</i>
                <i class="iconfont" v-if="item.avgScore>4">&#xe626;</i>
                <span class="commentNum">
                  <em>{{item.commentCount}}</em>
                </span>
              </div>
              <div class="price">
                <span class="price_p">
                  ¥
                  <em>{{item.qunarPrice}}</em>起
                </span>
                <span class="areaname">{{item.area}}</span>
              </div>
            </div>
          </div>
        </router-link>
      </ul>
	  	<router-link tag="a"  v-if="!isHome" class="showMore" :to="{name:'Search'}">查看所有产品</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  props: ["mayLikeList","isHome"],
  mounted() {
  }
};
</script>
<style lang='stylus' scoped>
.maylike {
  margin-top: 0.2rem;
  font-size: 0.32rem;
  line-height: 0.8rem;
  background-color: #fff;

  .title {
    text-indent: 0.2rem;
    text-align: left;

    img {
      width: 0.35rem;
      height: 0.35rem;
    }
  }
   h5 {
    line-height: 0.88rem;
    position: relative;
    text-indent: 0.2rem;
    border-bottom: 0.02rem solid #dadada;
    padding: 0 0.2rem;
  }

  h5::after {
    position: absolute;
    left: 0.2rem;
    top: 50%;
    height: 0.22rem;
    width: 0.04rem;
    background-color: #00bcd4;
    content: '';
    margin-top: -0.11rem;
  }

  .likeList {
    .item {
      overflow: hidden;
      position: relative;

      img {
        width: 2rem;
        height: 2rem;
        position: absolute;
        left: 0.05rem;
        top: 50%;
        margin-top: -1rem;
      }
    }

    .coreDetail {
      margin-left: 2.1rem;

      .title {
        color: #212121;
        font-size: 0.32rem;
        line-height: 0.44rem;
        height: 0.5rem;
        padding-top: 0.13rem;
        box-sizing: border-box;
      }

      .core {
        margin-top: 0.2rem;
        text-align: left;
        text-indent: 0.2rem;

        i {
          color: #ffb436;
          font-size: 14px;
        }

        .commentNum {
          font-size: 12px;
          margin-left: 0.2rem;
        }
      }

      .price {
        text-align: left;
        overflow: hidden;
        text-indent: 0.2rem;

        .price_p {
          color: #ff8300;
          font-size: 0.12rem;
          float: left;

          em {
            font-size: 16px;
          }
        }

        .areaname {
          float: right;
          font-size: 12px;
          margin-right: 0.2rem;
        }
      }
    }
  }
  .showMore{
	  display block;
	  width 100%;
	  height .9rem;
	  line-height .9rem;
	  text-align center;
	  font-size .26rem;
  }
}
</style>


