<!--  -->
<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="swiperList.length">
      <swiper-slide v-for="item in swiperList" :key="item.imgUrl">
		    <router-link :to="{'name':'Detail',query:{img:'http://img1.qunarzz.com/sight/p0/1604/78/78873494f26e554090.water.jpg_350x240_25bc5a06.jpg',title:city,sId:'1174758904'}}" class="swiper-img" tag="img" :src="item.imgUrl" :style="{'height':loop?'5.4rem':'2.4rem'}" alt></router-link>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination" :style="{'bottom':loop?'-1rem':'3px','color':loop?'#fff':'#fff'}"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "homeswiper",
  city:'',
  data() {
    return {
      swiperOption: {
        pagination: ".swiper-pagination",
        loop: this.loop ? false : true,
        autoplay:  this.loop ? false : 2000,
		autoplayDisableOnInteraction: false,
		paginationType:this.loop?'fraction':'bullets',
		observerParents:true,
		observer:true
      },
  
    };
  },
  props:['swiperList','loop'],
  mounted(){
  },
  created(){
	  this.city = this.$store.state.city;
  }
};
</script>
<style lang='stylus' scoped>
.wrapper >>> .swiper-pagination-bullet-active {
  background: #fff;
}

.wrapper {
  width: 100%;
  .swiper-img {
    width: 100%;
  }
}
.wrapper img {
  height 2.4rem
  width: 100%;
}
</style>


