<!--  -->
<template>
  <div class="search_page" style="overflow:auto">
    <div class="search_head">
      <div class="back" @click="back">
        <div class="iconfont">&#xe605;</div>
      </div>
      <div class="search_input">
        <input type="search" placeholder="输入城市或景点" v-model="searchInput">
      </div>
      <div class="search" @click="searchList">搜索</div>
    </div>
    <div style="position:relative">
      <div :class="['search_category',{'fixed':isFix}]">
        <div class="category_left">全部分类</div>
        <div
          class="category_right iconfont"
          @click="getRecommend"
          :style="{'color':isShow?'#00afc7':''}"
        >{{currentItem}}&nbsp;&#xe756;</div>
      </div>
      <div class="recommendList" v-show="isShow">
        <ul>
          <li
            :data-value="item.value"
            @click="chooseIt(index,item.title,item.value)"
            v-for="(item,index) in recommendItem"
            :key="index"
            :class="{'actives':index == current}"
          >{{item.title}}</li>
        </ul>
      </div>
    </div>
    <div class="maskMenu" v-show="isShow"></div>
    <div
      class="likeList"
      ref="wrapper"
      :style="{ height: (wrapperHeight) + 'px','background-color':'#fff' }"
    >
      <mt-loadmore
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded"
        ref="loadmore"
        :autoFill="isAutoFill"
      >
        <ul>
          <router-link
            :to="{'name':'Detail',query:{img:item.imgURL,title:item.name,sId:item.id}}"
            tag="li"
            v-for="(item,index) in mayLikeList"
            :key="index"
          >
            <div class="item">
              <img v-lazy="item.imgURL" :key="item.imgURL" class="itemCover" alt>
              <div class="coreDetail">
                <p class="title">{{item.name}}</p>
                <div class="core">
                  <i v-if="item.sightCommentScore>0" class="iconfont">&#xe626;</i>
                  <i v-if="item.sightCommentScore>1" class="iconfont">&#xe626;</i>
                  <i v-if="item.sightCommentScore>2" class="iconfont">&#xe626;</i>
                  <i v-if="item.sightCommentScore>3" class="iconfont">&#xe626;</i>
                  <i v-if="item.sightCommentScore>4" class="iconfont">&#xe626;</i>
                  <span class="commentNum">
                    <em>{{item.commentCount}}</em>条评论
                  </span>
                </div>
                <div class="price">
                  <span class="price_p">
                    ¥
                    <em>{{item.qunarPrice}}</em>起
                  </span>
                  <span class="areaname">{{item.hybridAddress}}</span>
                </div>
              </div>
            </div>
            <div v-if="item.priceList">
              <div class="ticket" v-for="(item1,index1) in item.priceList" :key="index1">
                <h4>{{item1.productName | cutStr(30,1)}}</h4>
                <span>¥{{item1.qunarPrice}}</span>
              </div>
            </div>
            <div style="height:.2rem;background:#f2f2f2"></div>
          </router-link>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      searchInput: "",
      recommendItem: [
        { title: "推荐排序", value: "" },
        { title: "离我最近", value: "dt" },
        { title: "人气最高", value: "pp" }
      ],
      isFix: false,
      isShow: false,
      current: "0",
      currentItem: "推荐排序",
      mayLikeList: [],
      currentPage: 2,
      allLoaded: false,
      isAutoFill: false,
      wrapperHeight: 0,
      sort: "",
      origin: "重庆"
    };
  },
  methods: {
    searchList() {
      this.getSearchList(this.searchInput);
      this.allLoaded = false;
    },
    getSearchList(keyword, sort, ss) {
      keyword = keyword || this.searchInput || "";
      switch (keyword) {
        case "重庆":
          keyword = "cq";
          break;
        case "北京":
          keyword = "bj";
          break;
        case "桂林":
          keyword = "gl";
          break;
        case "广州":
          keyword = "gz";
          break;
        case "上海":
          keyword = "sh";
          break;
        case "天津":
          keyword = "tj";
          break;
        default:
          keyword = "gl";
          break;
      }
      sort = sort || this.sort || "";
      this.$axios
        .get(
          "/" + keyword + "/list.json",
          "sort=" + sort + "&page=1&pageSize=10&keyword=" + keyword + ""
        )
        .then(res => {
          var data = res.data.data;
          if (ss) {
            this.mayLikeList = data.sightList.sort(function() {
              return 0.5 - Math.random();
            });
          } else {
            this.mayLikeList = data.sightList;
          }
        });
    },
    getRecommend() {
      this.isShow = this.isShow ? false : true;
    },
    chooseIt(index, title, value) {
      this.current = index;
      this.currentItem = title;
      this.isShow = false;
      this.sort = value;
      this.getSearchList(this.searchInput, this.sort, "1");
    },
    back() {
      this.backTo();
    },
    loadBottom() {
      this.$axios
        .get(
          "/gl/list.json",
          "page=" +
            this.currentPage +
            "&pageSize=10&keyword=" +
            this.searchInput +
            "&sort=" +
            this.sort +
            ""
        )
        .then(res => {
          if (!res.data.data) {
            // this.allLoaded = true; // 数据已全部获取完毕
            this.$toast("没有更多数据了~");
            this.$refs.loadmore.onBottomLoaded();
            return false;
          }
          var data = res.data.data;
          this.mayLikeList = this.mayLikeList.concat(data.sightList);
          this.currentPage++;
          this.$refs.loadmore.onBottomLoaded();
        });
    }
  },
  created() {
    this.searchInput = this.$route.query.area || this.$store.state.city;
    this.getSearchList();
  },
  mounted() {
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top;
  }
};
</script>
<style lang='stylus' scoped>
.fixed {
  position: fixed;
  top: 0.88rem;
  left: 0;
  z-index: 99;
  background-color: #fff;
}

.search_head {
  background-color: #fff;
  width: 100%;
  height: 0.88rem;
  display: flex;

  .back, .search {
    width: 0.8rem;
    height: 0.88rem;
    line-height: 0.88rem;
    font-size: 0.32rem;
    text-align: center;
  }

  .search_input {
    flex: 1;
    line-height: 0.88rem;

    input {
      border: 0;
      width: 100%;
      height: 0.6rem;
      border-radius: 50px;
      background-color: #f2f2f2;
      vertical-align: middle;
      padding-left: 0.3rem;
      font-family: 'Microsoft Yahei', Arial;
      text-align: center;
    }
  }

  .search {
    font-size: 0.28rem;
  }
}

.search_category {
  width: 100%;
  height: 0.8rem;
  display: flex;
  border: 1px solid #f2f2f2;
  border-left: 0;
  border-right: 0;
  background-color: #fff;

  .category_left, .category_right {
    flex: 1;
    text-align: center;
    line-height: 0.8rem;
    position: relative;
  }

  .category_left::after {
    content: '';
    display: inline-block;
    top: 50%;
    right: 0;
    width: 1px;
    height: 0.6rem;
    background-color: #f2f2f2;
    position: absolute;
    transform: translateY(-0.3rem);
  }
}

.recommendList {
  background-color: #fff;
  z-index: 999;
  position: absolute;
  top: 0.88rem;
  left: 0;
  width: 100%;
}

.recommendList ul li {
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
}

.recommendList ul li.actives {
  background-color: #f2f2f2;
}

.maskMenu {
  position: fixed;
  top: 4.12rem;
  background-color: rgba(0, 0, 0, 0.45);
  opacity: 0.5;
  width: 100%;
  height: 100%;
  z-index: 998;
}

.likeList {
  line-height: 0.8rem;
  background-color: #fff;

  .item {
    overflow: hidden;
    position: relative;
    background-color: #fff;

    img {
      width: 2rem;
      height: 2rem;
      position: absolute;
      left: 0.22rem;
      top: 50%;
      margin-top: -1rem;
    }
  }

  .coreDetail {
    margin-left: 2.25rem;
    background-color: #fff;

    .title {
      color: #212121;
      font-size: 0.32rem;
      line-height: 0.44rem;
      height: 0.5rem;
      padding-top: 0.13rem;
      box-sizing: border-box;
      text-align: left;
      padding-left: 0.2rem;
    }

    .core {
      margin-top: 0.2rem;
      text-align: left;
      text-indent: 0.2rem;

      i {
        color: #ffb436;
        font-size: 14px;
      }

      .commentNum {
        font-size: 12px;
        margin-left: 0.2rem;
      }
    }

    .price {
      text-align: left;
      overflow: hidden;
      text-indent: 0.2rem;

      .price_p {
        color: #ff8300;
        font-size: 0.12rem;
        float: left;

        em {
          font-size: 16px;
        }
      }

      .areaname {
        float: right;
        font-size: 12px;
        margin-right: 0.2rem;
      }
    }
  }
}

.ticket {
  border-top: 1px solid #f2f2f2;
  background-color: #fff;
}

.ticket h4 {
  display: inline-block;
  margin-right: 1.6rem;
  padding-left: 0.2rem;
}

.ticket span {
  float: right;
  margin-right: 0.3rem;
}

.actives {
  background-color: #f2f2f2;
}
</style>


