<!--  -->
<template>
  <div class="icons">
    <swiper :options="swiperOption" v-if="itemImg.length">
      <swiper-slide v-for="(page,index) in pages" :key="index">
          <router-link :to="{'name':'Detail',query:{img:'http://img1.qunarzz.com/sight/p0/1604/78/78873494f26e554090.water.jpg_350x240_25bc5a06.jpg',title:item.title,sId:'1174758904'}}" tag="div" class="item"  v-for="item in page" :key="item.title">
            <img :src="item.imgUrl" alt>
            <div class="icon_desc">{{item.title}}</div>
          </router-link>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
export default {
  name: "",
  data() {
    return {
      swiperOption: {
        pagination: ".swiper-pagination",
        loop: false,
        autoplay: false
      }
    };
  },
  props: ["itemImg"],
  computed: {
    pages() {
      const pages = [];
      this.itemImg.forEach((item, index) => {
        const page = Math.floor(index / 8);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(item);
      });
      return pages;
    }
  },
  mounted() {}
};
</script>
<style lang="stylus" scoped>
@import '~styles/mixins.styl';

.icons {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #fff;
  padding-top: 0.1rem;
  margin-top: 0.2rem;
}

.icons .swiper-container {
}

.icons .item {
  /* flex: 0 0 25%; */
  width: 25%;
  float: left;
  height: 1.82rem;
  /* border: 1px solid red */
  line-height: 1.82rem;
  position: relative;
  text-align: center;
}

.icons .item img {
  vertical-align: top;
  width: 1.14rem;
  height: 1.1rem;
}

.icons .item .icon_desc {
  position: absolute;
  left: 0;
  bottom: 0.3rem;
  display: inline-block;
  height: 0.32rem;
  line-height: 0.32rem;
  width: 100%;
  text-align: center;
  color: #212121;
  ellipsis();
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 0;
}
</style>


