<!--  -->
<template>
  <div class="more-recommend">
	   <backheader title="一周热销"></backheader>
	   <div class="cover">
		   <img src="http://img1.qunarzz.com/sight/p0/1606/ac/acada5bec3474afa90.water.jpg_375x170_48c838f6.jpg" alt="">
		  <div class="area-desc">	  	
				<h1><span class="h">一一</span><span class="iconfont">&#xe626;</span>{{this.$store.state.city}}<span class="iconfont">&#xe626;</span><span class="h">一一</span></h1>
				<h2>本周热门榜单</h2>
		  </div>
	   </div>
	   <may-like :mayLikeList="recommendList" :isHome="true"></may-like>
  </div>
</template>

<script>
import backheader from './components/backheader';
import mayLike from './components/maylike';
export default {
name:'',
  data () {
	return {
		recommendList:[]
	};
  },
  components:{
	  backheader,
	  mayLike
  },
  mounted(){
	  this.recommendList = JSON.parse(localStorage.getItem('maylike'));
  }
}

</script>
<style lang='stylus' scoped>
.more-recommend
	.cover
		position relative
		margin-top .88rem
		background-color #000
		img 
			opacity .9
		.area-desc
			position absolute
			top 1.2rem
			left 0
			width 100%
			text-align center
			color #fff
			h1
				font-size .36rem
				span
					margin 0 .2rem
					font-size .24rem
					vertical-align top 
				span.h
					font-weight bolder	
					margin 0 
			h2
				font-size: .44rem
				font-family: tahoma,arial,"Hiragino Sans GB","\5FAE\8F6F\96C5\9ED1",simsun,sans-serif
				font-weight bold
				margin-top .1rem
    			
			
			
				
			


</style>


