<!--  -->
<template>

  <div class="login-page">
	  <backheader title="登录/注册"></backheader>
	  <div class="header" @click="which">
		  <h5 class="login" :class="{'target':target}">登录</h5>
		  <h5 class="register" :class="{'target':!target}">注册</h5>
	  </div>
	  <div class="login-form" v-if="isLogin">
		<div class="rows">  <label>用户名：</label><input v-model="username" type="text" @input="inpU" placeholder="请输入用户名"></div>
		 <div class="rows"> <label>密　码：</label><input v-model="password" type="password" @input="inpP" placeholder="请输入密码"></div>
			<div class="login-btn"><a @click="toHome" :class="{'canLogin':canlogin}">登录</a></div>
	  </div>
	   <div class="login-form" v-if="!isLogin">
		<div class="rows">  <label>用户名：</label><input type="text" v-model="username" @input="inpU" placeholder="请输入用户名"></div>
		 <div class="rows"> <label>密　码：</label><input type="password" v-model="password" @input="inpP" placeholder="请输入密码"></div>
		  <!-- <div class="rows"> <label>重复密码：</label><input type="password" placeholder="请再次输入密码"></div> -->
		  <div class="rows">  <label>验证码：</label><input type="text" v-model="yzmcheck" @input="inpY" placeholder="请输入右侧验证码"><span @click="changeYzm" title="点击切换验证码" class="yzm" v-html="yzmString"></span></div>
			<div class="login-btn"><a style="background-color:#25a4bb" @click="register">注册</a></div>
	  </div>
  </div>
</template>

<script>
import backheader from './components/backheader';
import { fail } from 'assert';
import { setTimeout } from 'timers';
export default {
name:'',
  data () {
	return {
		opacity:1,
		showHeader:true,
		target:true,
		isLogin:true,
		yzmArr:[],
		yzmString:'',
		username:'',
		password:'',
		yzmcheck:'',
		canlogin:false,
		canregister:false
	};
  },
  components:{
	  backheader
  },
  methods:{
	  which(e){
		  if(e.target.className.toLowerCase().indexOf('login') > -1){
			  this.isLogin = true;
			  this.target = true
		  }else {
			  this.isLogin = false
			  this.target = false
		  }
		  this.username = '';
		  this.password = '';
		  this.yzmcheck = '';
		  this.canlogin = false;
	  },
	  yzm(){
		  for(var i=48;i<=122;i++){
			  if((i>=58 && i<=64) || (i>=91 && i<=96)){
				  continue;
			  }
			  this.yzmArr.push(String.fromCharCode(i))
		  }
		  this.changeYzm();
	  },
	  changeYzm(){
		  var tempYzm = [];
		  for(var i =0;i<4;i++){
			  var random = Math.floor(Math.random()*62)
			  tempYzm.push(this.yzmArr[random]);
		  }
		   this.yzmString = tempYzm.join(',').replace(/\,/g,'');
	  },
	  inpU(){
		if((this.username.length > 1 && this.username.length<13) && (this.password.length > 4 && this.password.length<19)){
			this.canlogin = true;
		}else {
			this.canlogin = false;
		}
	  },
	  inpP(){
		  if((this.username.length > 1 && this.username.length<13) && (this.password.length > 4 && this.password.length<19)){
			this.canlogin = true;
		}else{
			this.canlogin = false;
		}
	  },
	  inpY(){

	  },
	  toHome(){
		  if(this.canlogin){
			  if((localStorage.getItem('username') !== this.username) && localStorage.getItem('password') !== this.password){
				    this.$toast("请输入正确的账号或密码");
			  }else {
				  	localStorage.setItem('isLogin',1);
					this.$toast("登录成功~");
					// setTimeout(()=>{
					// 	this.$router.push({'name':'Home',query:{'islogin':true}})
					// },2000)
					setTimeout(()=>{
						this.$router.push(this.$route.query.redirect);
					},2000)
			  }		  
		  }else{
					  this.$toast("请输入正确的账号或密码");
					  localStorage.setItem('isLogin',0);
		  }
	  },
	  register(){
		 if(!(this.username.length > 1 && this.username.length<13) || !(this.password.length > 4 && this.password.length<19)){
			 this.$toast("用户名长度为限制为2-12，密码长度限制为5-18");
			 this.changeYzm();
		}else if(this.yzmcheck.toLowerCase() != this.yzmString.toLowerCase()) {
			  this.$toast("验证码输入错误");
			   this.changeYzm();
		}else {
			localStorage.setItem('username',this.username);
			localStorage.setItem('password',this.password);
			localStorage.setItem('userinfo','{"username":"'+this.username+'","password":"'+this.password+'"}')
			 this.$toast("注册成功~");
			 this.isLogin = true
		}
	  }
  },
  mounted(){
	  this.yzm();
  },
  watch:{
  },
  computed:{
  }
}

</script>
<style lang='stylus' scoped>
	.login-page
		height 100%
		background-color #f3f9fc
		.header
			margin-top .88rem
			display flex
			border-bottom .02rem solid #e2e2e2
			border-top .05rem solid #fff
			.login,.register 
				line-height .88rem
				text-align center
				flex 1
				color #19a9ba
				font-size .32rem
			.login
				border-right .02rem solid #e2e2e2
			.target
				background-color #00BCD4
				color #FFF
		.login-form
			padding 0 .2rem
			background-color #fff
			.rows
				border-bottom .02rem solid #f2f2f2
				font-size .3rem
				label
					line-height .88rem
					font-size .32rem
					color #19a9ba
				input
					padding-left 1rem	
					line-height .88rem
					font-family: 'Microsoft Yahei', Arial;
				.yzm
					line-height .6rem
					display inline-block
					height .6rem
					width 1.1rem
					border 1px solid #f1f1f1
					background-color #f1f1f1
					text-align center
					font-weight bold
					letter-spacing .04rem
			.login-btn
				margin-top .4rem
				a
					display block
					width 100%;
					line-height 1rem
					font-size .36rem
					color #fff
					background-color #85d1db
					text-align center
					border-radius .1rem
				.canLogin
					background-color #25a4bb


				


</style>


