<!--  -->
<template>
  <div>
<div class="header_fix">
      <span @click="back" class="iconfont backicon">&#xe605;</span>
      <h1 class="area">{{title}}</h1>
    </div>
  </div>
</template>

<script>
export default {
name:'',
  data () {
	return {
	};
  },
  props:['title'],
  methods:{
	  back(){
		  this.backTo();
	  }
  }
}

</script>
<style lang='stylus' scoped>
.header_fix 
	display: flex;
	background-color: #00bcd4;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99;
	width: 100%;
	margin-bottom .88rem
  	.backicon 
		width: 0.8rem;
		height: 0.88rem;
		display: inline-block;
		line-height: 0.88rem;
		text-align: center;
		color: #fff;
		font-size: 0.34rem;
	.area 
		display: inline-block;
		flex: 1;
		text-align: center;
		line-height: 0.88rem;
		font-size: 0.34rem;
		color: #fff;
		margin-right: 0.8rem;
  
</style>


