<!--  -->
<template>
  <div class="home">
    <header-component :city="city"></header-component>
    <home-swiper :swiperList="swiperList"></home-swiper>
    <honeIcons :itemImg="itemImg"></honeIcons>
    <recommend :recommendList="recommendList"></recommend>
    <may-like :mayLikeList="mayLikeList"></may-like>
    <weekend :WeekendList="WeekendList"></weekend>
    <footer-component v-if="isLogin"></footer-component>
  </div>
</template>

<script>
import changeCity from "./connect/chooseCity.js";
import Recommend from "./components/recommend";
import MayLike from "./components/maylike";
import Weekend from "./components/weekend";
import { get } from "http";
// import func from "../../vue-temp/vue-editor-bridge";
export default {
  name: "home",
  components: { Recommend, MayLike, Weekend },
  data() {
    return {
      city: "",
      swiperList: [],
      itemImg: [],
      recommendList: [],
      mayLikeList: [],
      WeekendList: [],
	  region: this.$store.state.city,
	  isLogin:false
    };
  },
  methods: {
    getInfo(city) {
	  city = city || this.region;
	  switch (city){
		  case '重庆':
			  city = 'cq';
			  break;
			  case '北京':
				  city = 'bj';
			  break;
			  case '桂林':
				  city = 'gl';
			  break;
			  case '广州':
				  city = 'gz';
			  break;
			  case '上海':
				  city = 'sh';
			  break;
			  case '天津':
				  city = 'tj';
			  break;
	  }
      this.$axios.get("/"+city+"/"+city+".json").then(res => {
        const data = res.data.data;
        this.city = data.city;
        this.swiperList = data.travelList;
        this.itemImg = data.tagList;
		this.recommendList = data.hotSale.hotSaleList;
        this.mayLikeList = data.guessULike.guessList.map((item)=>{
			item.id = this.getId(item.scheme);
			return item;
		});
		this.WeekendList = data.skuList;
		localStorage.setItem('maylike',JSON.stringify(this.mayLikeList))
      });
	},
    getId(val) {
		var newId = val.split('?')[1].split('&');
		for(var i=0;i<newId.length;i++){
			if(newId[i].indexOf('id=') > -1){
				return newId[i].split('=')[1];
				break;
			}
		}
    }
  },
  created() {
    let self = this;
    changeCity.$on("changecity", function(city) {
      self.getInfo(city);
	});
	this.isLogin = this.$route.query.islogin;
console.log(this.$store.state.city)
  },
  mounted() {
	 this.getInfo();
  }
};
</script>
<style lang='stylus' scoped>
.home {
  background-color: rgb(243, 243, 243);
}
</style>


