<!--  -->
<template>
  <div class="comment">
    <h5>用户评论</h5>
    <div class="comment-list">
      <ul>
        <li class="bt" v-for="(item,index) in commentList" :key="index" v-show="index < 2">
          <div class="scoredate">
            <span class="score">
              <i class="iconfont" v-if="item.score>0">&#xe626;</i>
              <i class="iconfont" v-if="item.score>1">&#xe626;</i>
              <i class="iconfont" v-if="item.score>2">&#xe626;</i>
              <i class="iconfont" v-if="item.score>3">&#xe626;</i>
              <i class="iconfont" v-if="item.score>4">&#xe626;</i>
            </span>
            <span class="date">{{item.author}}&nbsp;&nbsp;{{item.date}}</span>
          </div>
          <p
            class="comment-detail"
            :style="{'height':showmore === index ?'auto':''}"
          >{{item.content}}</p>
          <div
            class="comment-more iconfont"
            :ref="'no'+index"
            @click="showMore(index)"
            :style="{'transform':showmore === index ? 'rotate(180deg)':''}"
          >&#xe756;</div>
          <div class="pic-list" v-if="item.imgs">
			<span class="pic-count" v-show="item.imgs.length > 5">
              <span style="margin-right:.08rem" class="iconfont backicon">&#xe6dd;</span>{{item.imgs.length}}
            </span>
            <div
              class="pic"
              v-for="(pic,index) in item.imgs"
              :key="index"
              v-show="index<6"
              @click="picShow(item.imgs)"
            >
              <img :src="pic.big" alt>
            </div>
          </div>
        </li>
      </ul>
      <router-link
        :to="{'name':'AllComment'}"
        tag="div"
        class="all-comment iconfont"
      >查看全部评论&nbsp;&#xe63f;</router-link>
    </div>
    <gallary v-if="showPic" :swiperList="swiperList" @close="close"></gallary>
  </div>
</template>

<script>
import gallary from "./gallary";
import { debug } from "util";
export default {
  name: "comment",
  data() {
    return {
      showmore: "",
      commentList: [],
      showPic: false,
	  swiperList: [],
	   cl:[
		  '秋天是落叶之美，一阵阵瑟瑟的风吹过之后，夏走了，秋来了。树叶由原来的繁密到枯黄，再落下。这是一个过程，但也十分美丽。我走在山路上，密密落下的枯树叶铺成一条金黄色的路，漫步其中，身心好似与自然融为一体。我随手拾起一片落叶，那枯黄的叶子上还微微透出些青色。',
		  '湖面很平静，水清清的，高空的白云和周围的山峰清晰地倒映在水中，湖水天影融为一体。在这幽静的湖中，唯一浮动的就是一些飞鸟了。人们说出山色多变，其实，湖色也多变，淡蓝、深蓝、浅绿、墨绿，在湖面上，界限分明。',
		  '河的两岸就是游园，人们称它叫“千亩游园”。走进游园，首先是一片梧桐树林。树林里，一棵棵梧桐树就像战士一样笔直地站在那里。树上枝繁叶茂，大大的叶子挤在一起，好像给游园戴了一顶大大的帽子，把阳光都挡在了外面。所以，现在即使是夏天，走进这个树林也感觉非常凉爽。',
		  '如果要用一种颜色来描绘一个你喜欢的季节你会怎样说?春，绿意盎然;夏，花团锦簇，所以是红色;冬，银装素裹;那秋呢--自然是那一片单调的金色。而我却独爱这单调的金色。可能是我生在冬天的缘故吧，冬天是银色，秋天是金色，所以秋冬是好友。自然，我也是把秋当做了我的好友。' ,
		  '透过我家的一扇窗户向外看，一排排像军人的树已经苦苦地熬过严冬，再次长出树叶，那碧绿的树叶就像一件完美的军服，给这些熬过漫长严冬的军人们送上温暖。小鸟们被鸟巢里的闷气受够了，它们在户外呼吸新鲜的空气，一丝丝春风带给它们无比凉爽，小鸟唱起动听的歌，歌唱它们的无比自豪。每一个角落都在变，变成比严冬，比秋天，比仲夏不一样的家园。',
		  '秋天，没有春天五彩斑斓的鲜花，没有夏天茂盛异常的绿树，没有冬天纷纷扬扬的雪花，但秋天有果实累累的果树，给人们带来丰收的喜悦，到处都是瓜果遍地!秋天，没有春天五彩斑斓的鲜花，没有夏天茂盛异常的绿树，没有冬天纷纷扬扬的雪花，但秋天有果实累累的果树，给人们带来丰收的喜悦，到处都是瓜果遍地!'
	  ]
    };
  },
  props: ["sId"],
  methods: {
    showMore(index) {
      if (this.showmore == index) {
        this.showmore = this.showmore + "u";
      } else {
        this.showmore = index;
      }
    },
    picShow(item) {
      this.swiperList = item;
      this.showPic = true;
    },
    close() {
      this.showPic = false;
    }
  },
  components: {
    gallary
  },
  mounted() {
    var keyword = "";
    switch (this.$store.state.city) {
      case "重庆":
        keyword = "cq";
        break;
      case "北京":
        keyword = "bj";
        break;
      case "桂林":
        keyword = "gl";
        break;
      case "广州":
        keyword = "gz";
        break;
      case "上海":
        keyword = "sh";
        break;
      case "天津":
        keyword = "tj";
        break;
      default:
        keyword = "gl";
        break;
    }
    this.$axios.get("/" + keyword + "/pl.json").then(res => {
      const data = res.data.data;
      data.commentList.map(item => {
        if (item.content.length < 130) {
             item.content +=this.cl[Math.floor(Math.random()*this.cl.length)];
        }
        if (item.imgs.length > 0) {
          item.imgs.map(i => {
            i.imgUrl = i.big;
          });
        }
        return item;
      });
	  this.commentList = data.commentList.sort(function() {
            return 0.5 - Math.random();
          });
    });
  }
};
</script>
<style lang='stylus' scoped>
.comment {
  background-color: #ffffff;
  margin-top: 0.2rem;

  h5 {
    line-height: 0.88rem;
    position: relative;
    text-indent: 0.2rem;
    border-bottom: 0.02rem solid #dadada;
    padding: 0 0.2rem;
  }

  h5::after {
    position: absolute;
    left: 0.2rem;
    top: 50%;
    height: 0.22rem;
    width: 0.04rem;
    background-color: #00bcd4;
    content: '';
    margin-top: -0.11rem;
  }

  .comment-list {
    padding: 0 0.2rem 0rem 0.2rem;

    .all-comment {
      line-height: 0.88rem;
      text-align: center;
      color: #616161;
      font-size: 0.26rem;
    }

    li {
      padding-bottom: 0.2rem;
      box-sizing: border-box;
      border-bottom: 1px dashed #e2e2e2;

      .scoredate {
        margin-top: 0.3rem;

        .score i {
          color: #ffb436;
          font-size: 0.24rem;
          transform: scale(0.8);
        }

        .date {
          float: right;
          color: #212121;
          font-size: 0.24rem;
        }
      }

      .comment-detail {
        margin-top: 0.2rem;
        height: 2.1rem;
        overflow: hidden;
        word-break: break-all;
        word-wrap: break-word;
        line-height: 0.42rem;
        font-size: 0.26rem;
        color: #616161;
      }

      .comment-more {
        text-align: center;
        line-height: 0.48rem;
        font-size: 0.5rem;
        color: #9e9e9e;
      }

      .pic-list {
        position: relative;

        .pic-count {
          background-color: rgba(0, 0, 0, 0.3);
          position: absolute;
          width: 1rem;
          height: 0.3rem;
          color: #ffffff;
          line-height: 0.3rem;
          text-align: center;
          border-radius: 0.1rem;
		  bottom .16rem;
		  right .2rem;
        }

        .pic {
          display: inline-block;
          width: 32.5%;
          height: 1.64rem;
          margin: 0 0.05rem 0.05rem 0;

          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
</style>


