<!--  -->
<template>
  <div class="recommend">
    <div style="overflow:hidden">
      <div class="recommend-title">
        <img src="http://img1.qunarzz.com/piao/fusion/1711/16/bfbb9874e8f11402.png" alt>
        热销推荐
      </div>
      <div class="moreRecommeng" @click="moreList">全部榜单 ></div>
    </div>
    <div class="imgList">
      <ul>
		  <router-link :to="{'name':'Detail',query:{img:item.imgUrl,title:item.title,sId:item.id}}" tag="li" v-for="(item,index) in recommendList" :key="item.imgUrl">	  
          <img v-lazy="item.imgUrl" alt>
          <span v-show="index < 3" :class="'istop'+(index+1)">Top.{{index+1}}</span>
		  <p class="area">{{item.title | cutStr(10,1)}}</p>
		  <div class="price"><i>¥{{item.qunarPrice}}</i>起</div>
			</router-link>     
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "recommend",
  data() {
    return {
    
    };
  },
  props:['recommendList'],
  methods:{
	  moreList(){
		  this.$router.push({path:'/recommend'})
	  }
  },created(){},
  mounted(){
  }
};
</script>
<style scoped>
.recommend {
  background-color: #fff;
  margin-top: .2rem;
}
.recommend-title {
  line-height: 0.8rem;
  text-align: left;
  text-indent: 0.2rem;
  /* margin-top: .2rem; */
  font-size: 0.32rem;
  float: left;
}
.recommend-title img {
  width: 0.35rem;
  height: 0.35rem;
}
.moreRecommeng {
  line-height: 0.8rem;
  text-align: left;
  text-indent: 0.2rem;
  /* margin-top: .2rem; */
  font-size: 0.22rem;
  float: right;
  margin-right: 0.2rem;
}
.imgList ul {
  overflow-x: auto;
  white-space: nowrap;
  height: 3rem;
  margin-left: 0.15rem;
  padding-top: 0.1rem;
}
.imgList ul::-webkit-scrollbar {
	display: none
}
.imgList ul li {
  display: inline-block;
  margin-right: 0.15rem;
  position: relative;
}
.imgList ul li img {
  width: 2rem;
  height: 2rem;
}
.imgList ul li span {
  width: 0.84rem;
  height: 0.4rem;
  display: inline-block;
  position: absolute;
  left: 0;
  top: -0.08rem;
  color: #fff;
  font-weight: bold;
  line-height: 0.4rem;
}
.imgList ul li span.istop1 {
  background-color: red;
}
.imgList ul li span.istop2 {
  background-color: orange;
}
.imgList ul li span.istop3 {
  background-color: yellow;
}
.area {
	margin-top: .2rem;
	font-size: .3rem;
	margin-bottom: .1rem;
}
.price i {
	color: #ff8300;
}

</style>


