
<template>
  <div class="detail">
    <gallary v-if="isShowGallary" @close="close" :swiperList="swiperList"></gallary>
    <div class="header_fix" v-show="showHeader" :style="{'opacity':opacity}">
      <span @click="back" class="iconfont backicon">&#xe605;</span>
      <h1 class="area">{{title}}</h1>
    </div>
    <div class="detail_header">
      <div class="header_img" >
        <span @click="back" v-show="!showHeader" class="s_back backicon iconfont" style="z-index:99">&#xe605;</span>
        <img 
          style="height:4.12rem;width:100%"
		  :src="currentImg"
          alt
		  @click="handleImgClick"
        >
        <div class="picNum">
          <span class="iconfont backicon">&#xe6dd;</span>
          <em style="padding-left:.1rem">{{swiperList.length}}</em>
        </div>
        <div class="title">{{title}}(AAAAA景区)</div>
      </div>
      <div class="category">
        <div class="category_left">
          <p class="score_d">
            <span class="score">5.0</span>分&nbsp;&nbsp;&nbsp;
            <span class="score_ac">超赞</span>
          </p>
          <div class="comment">
            <span class="comment_num">392111条评论</span>
            <span class="comment_skill">93条攻略</span>
          </div>
          <span class="iconfont more">&#xe63f;</span>
        </div>
        <div class="category_right">
          <h4 class="desc">景点简介</h4>
          <div class="open">开放时间、贴士</div>
          <span class="iconfont more">&#xe63f;</span>
        </div>
      </div>
      <div class="map">
        <span style="margin-right:.2rem;margin-left:.2rem" class="iconfont">&#xe617;</span>{{this.$store.state.city}}市{{title}}街4号
        <span style="margin-right:.2rem" class="iconfont more">&#xe63f;</span>
      </div>
    </div>
    <div class="warn">
      <span class="iconfont" style="color:#ff8300">&#xe646;</span>
      <span
        class="warn-content"
      >1.如遇{{title}}戒严，请从{{title}}两侧（东华门或西华门），沿故宫城墙步行至午门验票！2.故宫博物院2019年延续周一闭馆（国家法定节假日除外）的措施，具体时间请以景区官方通知为准，给您带来不便敬请谅解。</span>
      <span class="iconfont">&#xe63f;</span>
    </div>
    <div class="detail_tip" style>
      <h2 class="tip-title">
        <span class="iconfont zan">&#xe639;</span>去哪儿推荐
      </h2>
      <div class="tip-detail" v-for="(item,index) in tipList" :key="index">
        <div class="tip-left">
          <h3 class="title">{{item.title}}</h3>
          <div class="time">
            <div style="font-size:.24rem;height:.32rem;line-height:.32rem;">
              <span class="iconfont" style="color:#00bcd4;">&#xe6af;</span>
              <span style="padding-left:.1rem">{{item.time}}</span>
            </div>
            <div class="tag">
              <span v-for="i in item.tag" :key="i">{{i}}</span>
            </div>
          </div>
        </div>
        <div class="tip-right">
          <div class="price">
            <strong>¥{{item.price}}</strong>
            <router-link tag="button" :to="{name:'OrderTicket'}" class="order">预定</router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="detail_recommend">
      <div :class="[{'fixed':isFix},'recommend_category']">
        <ul>
          <li
            @click="curCate(index,item.ref)"
            v-for="(item,index) in category"
            :class="{'on':index == current}"
            :ref="item.ref"
            :key="index"
          >{{item.title}}</li>
        </ul>
      </div>
      <div
        class="category-list"
        v-for="(item,index) in category"
        :ref="item"
        :key="index"
        :style="{'margin-top':index !=0 ? '.2rem':''}"
      >
        <h3>
          <span class="iconfont" style="color:#00dcb4;margin-right:5px">&#xe647;</span>
          {{item.title}}
        </h3>
        <ul>
          <li v-for="(item,index) in categoryList" :key="index">
            <h5>{{item.title}}</h5>
            <h6>
              ¥
              <em>{{item.price}}</em>
              <span>起</span>
              <i class="iconfont" style="color:#bbb;">&#xe756;</i>
            </h6>
          </li>
        </ul>
      </div>
    </div>
    <comment :sId="sId"></comment>
    <maylike :mayLikeList="mayLikeList" :isHome="true"></maylike>
  </div>
</template>

<script>
import gallary from "./components/gallary";
import { clearTimeout, setTimeout } from "timers";
import Bscroll from "better-scroll";
import $ from "jquery";
import comment from "./components/comment";
import maylike from "./components/maylike";
export default {
  name: "",
  data() {
    return {
      isShowGallary: false,
      timer: null,
      showHeader: false,
      opacity: 0,
	  isFix: false,
	  currentImg:'http://img1.qunarzz.com/sight/p0/1604/78/78873494f26e554090.water.jpg_350x240_25bc5a06.jpg',
	  title:'迪士尼乐园',
	  sId:'1174758904',
      tipList: [],
      categoryClass: ["mp", "yry", "jqfw", "rxzh"],
      category: [
        { title: "门票", ref: "mp" },
        { title: "一日游", ref: "mp" },
        { title: "景区服务", ref: "mp" },
        { title: "热销组合", ref: "mp" }
      ],
      categoryList: [],
      current: 0,
	  mayLikeList: [],
	  swiperList:[{
          "id": "0001",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1508/89/895a1b7add84f23faca053ce9e3153db.water.jpg_350x240_5291130a.jpg"
        },{
          "id": "0002",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1708/91/91673f2642a275ada3.water.jpg_350x240_b4d47ff7.jpg"
        },{
          "id": "0003",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1708/58/5889f01c58f85bdda3.water.jpg_350x240_c75a1824.jpg"
        },{
          "id": "0004",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1808/13/132d71e2d3d30986a3.img.jpg_350x240_ef070bce.jpg"
		},
		{
          "id": "0010",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1805/ad/ad507355a167f587a3.img.jpg_350x240_1001da6d.jpg"
		}
		,{
          "id": "0005",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1805/ba/ba56a51849aa0f7ea3.img.jpg_350x240_04009ef5.jpg"
		}
		,{
          "id": "0006",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/1805/b2/b24d06e339164ee2a3.img.jpg_350x240_df02aec7.jpg"
		},
		{
          "id": "0007",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/201302/05/8f28e80ee761f6a9c8d65eac.jpg_350x240_a0dfbe61.jpg"
		}
		,{
          "id": "0008",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/201407/09/dc85c8576d5f6f1b8a04ca3022b9028b.jpg_350x240_4c6f15d6.jpg"
		}
		,{
          "id": "0009",
          "imgUrl": "http://img1.qunarzz.com/sight/p0/201407/09/4a35928ce76cc3fe81f6d6cf9c25e05a.jpg_350x240_7dcf6030.jpg"
		}],
    };
  },
  components: {
    gallary,
    comment,
    maylike
  },
  methods: {
    handleImgClick() {
      this.isShowGallary = true;
    },
    back() {
		this.backTo();
	},
    close() {
      this.isShowGallary = false;
    },
    scroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 60) {
        let opacityStyle = scrollTop / 140;
        opacityStyle = opacityStyle > 1 ? 1 : opacityStyle;
        this.opacity = opacityStyle;
        this.showHeader = true;
      } else {
        this.opacity = 0;
        this.showHeader = false;
      }
      if (scrollTop > 717) {
        this.isFix = true;
        if (scrollTop > 717 && scrollTop < 953) {
          this.current = 0;
        } else if (scrollTop > 965 && scrollTop < 1250) {
          this.current = 1;
        } else if (scrollTop > 1251 && scrollTop < 1542) {
          this.current = 2;
        } else if (scrollTop > 1543 && scrollTop < 1840) {
          this.current = 3;
        } else {
          this.isFix = false;
        }
      } else {
        this.isFix = false;
      }
    },
    curCate(index, ref) {
      switch (index) {
        case 0:
          $("html,body").animate({ scrollTop: "718px" }, 100);
          break;
        case 1:
          $("html,body").animate({ scrollTop: "966px" }, 100);
          break;
        case 2:
          $("html,body").animate({ scrollTop: "1252px" }, 100);
          break;
        case 3:
          $("html,body").animate({ scrollTop: "1544px" }, 100);
          break;
      }
    }
  },
  created() {
	this.mayLikeList = JSON.parse(localStorage.getItem("maylike"));
	this.currentImg = this.$route.query.img || this.currentImg;
	this.title = this.$route.query.title || this.title;
	this.sId = this.$route.query.sId || this.sId;
	this.$store.state.cityId=this.sId;
	this.swiperList.unshift({"id": "0111","imgUrl": this.currentImg})
	this.tipList = [ {
          title: "【上午场】"+this.title+"成人票凭身份证快速入园（旺季）",
          time: "23:59前可订明日",
          price: "61",
          tag: ["自营", "无需换票", "无条件退"]
        },
        {
          title: "【下午场】"+this.title+"成人票凭身份证快速入园（旺季）",
          time: "23:59前可订明日",
          price: "61",
          tag: ["自营", "无需换票", "返利"]
        },
        {
          title: ""+this.title+"大门票+珍宝馆+资深导游讲解（配无线耳麦）*成人票",
          time: "23:59前可订明日",
          price: "99",
          tag: ["自营", "无条件退"]
		}]
		this.categoryList = [
		{ title: "上午场", price: "85" },
        { title: "下午场", price: "90" },
        { title: ""+this.title+"+珍宝馆+钟表馆", price: "70" },
        { title: ""+this.title+"+珍宝馆", price: "75" },
        { title: ""+this.title+"+钟表馆", price: "100" }
		]
  },
  mounted() {
    window.addEventListener("scroll", this.scroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scroll);
  }
};
</script>

<style lang='stylus' scoped>
.header_fix {
  display: flex;
  background-color: #00bcd4;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;

  .backicon {
    width: 0.8rem;
    height: 0.88rem;
    display: inline-block;
    line-height: 0.88rem;
    text-align: center;
    color: #fff;
    font-size: 0.34rem;
  }

  .area {
    display: inline-block;
    flex: 1;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.34rem;
    color: #fff;
    margin-right: 0.8rem;
  }
}

.detail_header {
  .header_img {
    position: relative;

    .s_back {
      position: absolute;
      top: 0.2rem;
      left: 0.2rem;
      background-color: rgba(0, 0, 0, 0.5);
      color: #fff;
      font-size: 16px;
      border-radius: 50%;
      display: inline-block;
      width: 0.72rem;
      height: 0.72rem;
      line-height: 0.72rem;
      text-align: center;
    }

    .header_img img {
      height: 4.12rem;
    }

    .picNum {
      padding: 0.1rem 0.2rem;
      position: absolute;
      bottom: 0.72rem;
      left: 0.4rem;
      background-color: rgba(0, 0, 0, 0.3);
      border-radius: 0.4rem;
      color: #fff;
    }

    .title {
      position: absolute;
      bottom: 0;
      left: 0;
      padding-left: 0.4rem;
      background-color: rgba(0, 0, 0, 0.5);
      color: #fff;
      font-size: 16px;
      padding: 0.2rem;
      width: 100%;
      box-sizing: border-box;
    }
  }

  .category {
    height: 1rem;
    background-color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    position: relative;
    top: -0.1rem;
    box-sizing: border-box;
    padding: 0.2rem;
    padding-bottom: 0rem;

    .category_left {
      flex: 1;
      position: relative;

      .score_d {
        color: #ff8300;

        .score {
          font-size: 0.4rem;
        }
      }

      .comment_num {
        color: #9e9e9e;
        line-height: 0.4rem;
        font-size: 0.24rem;
      }

      .comment_skill {
        color: #9e9e9e;
        line-height: 0.4rem;
        font-size: 0.24rem;
        margin-left: 0.1rem;
      }
    }

    .category_left::after {
      content: '';
      display: inline-block;
      top: 50%;
      right: 0;
      width: 1px;
      height: 0.6rem;
      background-color: #f2f2f2;
      position: absolute;
      transform: translateY(-0.3rem);
    }

    .category_right {
      flex: 1;
      padding-left: 0.5rem;
      position: relative;

      .open {
        color: #9e9e9e;
        line-height: 0.4rem;
        font-size: 0.24rem;
        margin-top: 0.05rem;
      }
    }
  }

  .map {
    background-color: #ffffff;
    padding: 0.3rem 0.1rem;
    position: relative;
    top: -0.1rem;
  }
}

.more {
  position: absolute;
  right: 0.2rem;
  top: 50%;
  margin-top: -8px;
}

.fixed {
  position: fixed;
  top: 0.88rem;
  left: 0;
  z-index: 99;
  background-color: #fff;
}

.zan {
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  background-color: #ff6e6e;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  line-height: 0.4rem;
  color: #fff;
  margin-right: 0.1rem;
}

.warn {
  background-color: #fff5e5;
  height: 0.88rem;
  line-height: 0.88rem;
  padding: 0 0.2rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.warn .warn-content {
  display: inline-block;
  color: #ff8300;
  margin-right: 0.4rem;
}

.detail_recommend {
  margin-top: 0.2rem;
  box-sizing: border-box;
  color: #333;
  line-height: 0.88rem;

  .recommend_category {
    border-bottom: 0.01rem solid #dadada;
    background-color: #fff;
    height: 0.88rem;
  }

  .category-list {
    background-color: #fff;
    padding: 0 0.2rem;

    h3 {
      font-size: 0.32rem;
      border-bottom: 0.01rem solid #dadada;
    }

    li {
      border-bottom: 0.01rem solid #dadada;
      display: flex;

      h5 {
        display: inline-block;
        flex: 4;
      }

      h6 {
        display: inline-block;
        flex: 2;
        color: #ff9800;
        text-align: right;

        em {
          color: #ff9800;
          font-size: 0.4rem;
        }

        span {
          font-size: 0.24rem;
          color: #9e9e9e;
          transform: scale(0.8);
        }
      }
    }
  }
}

.detail_recommend .recommend_category ul {
  overflow-x: auto;
  white-space: nowrap;
}

.detail_recommend .recommend_category ul::-webkit-scrollbar {
  display: none;
}

.detail_recommend .recommend_category ul li {
  width: 2rem;
  text-align: center;
  display: inline-block;
  font-size: 0.32rem;
  position: relative;
}

.detail_recommend .recommend_category ul li.on::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -0.75rem;
  content: '';
  width: 1.5rem;
  height: 0.04rem;
  background-color: #00bcd4;
}

.detail_tip {
  width: 100%;
  padding: 0 0.2rem;
  background-color: #fff;
  box-sizing: border-box;
  margin-top: 0.2rem;

  .tip-title {
    padding: 0.2rem 0 0.2rem 0;
    font-size: 0.32rem;
    border-bottom: 1px solid #f2f2f2;
  }

  .tip-detail {
    box-sizing: border-box;
    border-bottom: 1px solid #f2f2f2;
    display: flex;
    flex-direction: columns;
    padding: 0.2rem 0.2rem 0.24rem 0;

    .tip-left {
      flex: 3;
      margin-right: 0.2rem;
      line-height: 0.52rem;

      h3 {
        font-size: 0.3rem;
        color: #616161;
      }

      .tag {
        span {
          border: 1px solid #00bcd4;
          text-align: center;
          font-size: 0.2rem;
          margin: 0.05rem;
          color: #00bcd4;
          transform: scale(0.33333);
        }
      }
    }

    .tip-right {
      flex: 1;
      padding: 0.3rem 0.2rem 0.2rem 0;

      .price {
        strong {
          color: #ff9800;
          display: block;
          text-align: center;
          margin-top: 0.2rem;
          font-size: 0.32rem;
          font-weight: bolder;
        }

        .order {
          width: 100%;
          height: 0.6rem;
          background-color: #ffab16;
          color: #fff;
          text-align: center;
          border-radius: 0.1rem;
        }
      }
    }
  }
}
</style>


