<!--  -->
<template>
  <div class="weekend">
    <h2>周末去哪儿</h2>
    <div class="recommend">
      <ul>
        <router-link tag="li" :to="{'name':'Detail'}" v-for="item in WeekendList" :key="item.imgUrl">
          <div class="area">
            <img
              v-lazy="item.imgUrl"
              alt
            >
          </div>
          <div class="areaInfo">
            <p class="title">{{item.title}}</p>
            <p class="desc">{{item.desc}}</p>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {};
  },
  props:['WeekendList']
};
</script>
<style lang='stylus' scoped>
@import '~styles/mixins.styl'
.weekend {
  background-color: #fff;
  margin-top: 0.2rem;
  h2 {
	  padding .3rem .2rem;
	  text-align left 
  }
}

.weekend .recommend ul li {
  .area {
    width: 100%;
    height: 2.8rem;
  }

  .area img {
    width: 100%;
    height: 100%;
  }
	.areaInfo {
		box-sizing border-box
		width 100%;
		padding .2rem .4rem .15rem .2rem;
		text-align left ;
		.title {
			padding .05rem;
			font-size .26rem
			color: #212121;
		}
		.desc {
			margin-top .1rem
			font-size .24rem
			color: #616161;
			height .3rem;
			line-height .3rem;
			ellipsis()
		}
	}
  
}
</style>


