import Vue from "vue";
import Vuex from 'vuex';
Vue.use(Vuex)
export default new Vuex.Store({
	state:{
		city:'桂林',
		cityId:''
	},
	// actions:{
	// 	changeCity(ctx,city){
	// 		ctx.commit('changeCity',city)
	// 	}
	// },
	mutations:{
		changeCity(state,val){
			state.city = val;
		}
	}
})



