<!--  -->
<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe605;</div>
    </div>
    <router-link :to="{name:'Search',query:{'area':city}}">
      <div class="header-input">
        <span class="iconfont">&#xe633;</span>
        输入城市/景点/游玩主题
      </div>
    </router-link>
    <div class="header-right" style="position:relative" @click="showCityList">
     {{city}}
      <span class="iconfont arrow-icon">&#xe756;</span>
      <div class="cityList" v-show="isShowCity">
        <ul>
          <li @click="chooseCity(item.name)" v-for="(item,index) in cityList" :key="index">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import changeCity from '../connect/chooseCity.js';
export default {
  name: "",
  data() {
    return {
      cityList: [
        { name: "重庆" },
        { name: "上海" },
        { name: "天津" },
        { name: "北京" },
		{ name: "广州" },
		{ name: "桂林" },
	  ],
	  city:this.$store.state.city,
	  isShowCity:false
    };
  },
  methods:{
	  chooseCity(name){
		  this.city = name;
		  changeCity.$emit('changecity',this.city);
		//   this.$store.dispatch('changeCity',this.city);
		  this.$store.commit('changeCity',this.city);
	  },
	  showCityList(){
		  this.isShowCity = this.isShowCity ? false : true;
	  }
  }
};
</script>
<style lang='stylus' scoped>
@import '~styles/varibles.styl';

.header {
  line-height: 0.86rem;
  background-color: $bgColor;
  display: flex;
  color: #fff;

  .header-left {
    width: 0.64rem;
	text-align center;
    float left {
      .back-icon {
        text-align: center;
        font-size: 0.4rem;
      }
    }
  }

  a {
    flex: 1;
  }

  .header-right {
    width: 1.24rem;
    float: right;
    text-align: center;
  }
  .cityList {
	  position absolute;
	  left 0;
	  top .81rem;
	  background-color #fff
	  z-index 999;
	  width 100%
  }
  .cityList ul li {
	  color #fff;
	  background-color #00bcd4;
	  border-bottom 1px dashed #e5e5e5;
	  height .6rem
	  line-height .6rem
  } 

  .header-input {
    flex: 1;
    background-color: #ffffff;
    border-radius: 0.1rem;
    margin-top: 0.12rem;
    height: 0.64rem;
    margin-left: 0.2rem;
    color: #ccc;
    text-align: left;
    line-height: 0.64rem;
    margin-left: 0.2rem;
    padding-left: 0.2rem;
  }

  .arrow-icon {
    font-size: 0.24rem;
    margin-left: -0.04rem;
  }
}
</style>


