<!--  -->
<template>
  <div class="container" @click="showGallary">
	  <div class="swapper">
			<swiper :options="swiperOption" v-if="swiperList.length">
			<swiper-slide v-for="item in swiperList" :key="item.id">
					<img :to="{'name':'Detail'}" class="swiper-img" tag="img" :src="item.imgUrl" style="height:5.4rem" alt>
			</swiper-slide>
			<div class="swiper-pagination" slot="pagination" style="bottom:-127px;color:#fff"></div>
			</swiper>
	  </div>
  </div>
</template>

<script>

export default {
name:'',
  data () {
	return {
		swiperOption: {
			pagination: ".swiper-pagination",
			paginationType:'fraction',
			observerParents:true,
			observer:true
		}
	};
  },
  mounted(){
  },
  props:['swiperList'],
  methods:{
	  showGallary(){
		this.$emit('close')
	  }
  }
}

</script>
<style lang='stylus' scoped>
	.container >>> .swiper-container
		overflow inherit
	.container
		position fixed
		top 0
		left 0
		bottom 0
		right 0
		background-color #000
		z-index 99
		display flex
		flex-direction column
		justify-content center
		.swapper
			height: 0
			width: 100%
			padding-bottom: 100%;
			img 
				width 100%
</style>


